'use client'
import { useEffect, useState } from 'react'

export default function useAuth() {
  const [account, setAccount] = useState(null)

  useEffect(() => {
    const sessionId = localStorage.getItem('tmdb_session')
    const storedAccount = localStorage.getItem('tmdb_account')

    if (sessionId && storedAccount) {
      try {
        const parsed = JSON.parse(storedAccount)
        setAccount(parsed)
      } catch (err) {
        console.error('Error parsing account data', err)
      }
    }
  }, [])

  return { account }
}
